.widgets{
    padding: 25px 10px;
    flex: 0.33 1 123px;
    width: 100%;
    align-self: flex-start;
    position: sticky;
    top: 64px;
    left: 0px;
    height: 90vh;
    overflow: auto;
    background-color: rgba(0,0,0,0);
    -webkit-background-clip: text; /*chỉ hieu ung cho scrollbar*/
    transition: background-color .5s;
}

/* css scrollbar */
.widgets::-webkit-scrollbar{ /*nền scroll*/
    width: 8px;
	background-color: #F5F5F5;
    
}

.widgets:hover{
    background-color: rgba(0,0,0,.164);
}

.widgets::-webkit-scrollbar-thumb{
    display: none;
}

.widgets:hover::-webkit-scrollbar-thumb
{
    display: block;
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px inherit;
	background-color: inherit;
}


/* end-css-scrollbar */
.widgets__navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: gray;
}

.widgets__icon{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}