*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
    background-color: #f1f2f5;
}

.app__body{
    display: flex;
    justify-content: space-between;
}
.header{
    display: flex;
    padding: 5px 20px;
    justify-content: space-between;
    position: -webkit-sticky;
    position: sticky;
    background-color: white;
    z-index: 100;
    top: 0;
    box-shadow: 0px 5px 8px -9px rgba(0, 0, 0, 0.9);
}

.header__left{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.header__left > img{
    height: 40px;
}

.header__input{
    display: flex;
    align-items: center;
    padding: 10px;
    margin-left: 10px;
    background-color: #eff2f5;
    border-radius: 33px;
}

.header__input > input{
    border: none;
    outline: none;
    background-color: transparent;
}

.header__middle{
    display: flex;
    align-items: center;
    padding: 0px 30px;
    cursor: pointer;

}

.header__options{
    padding: 7px 30px;
    margin: 0 10px;
    border-radius: 10px;
    position: relative;
}

.header__options:hover{
    background-color: #eff2f5;
}


.header__options  .MuiSvgIcon-root{
    color: gray;
}

.header__options:first-child::after{
    content: '';
    position: absolute;
    display: block;
    bottom: -6px;
    left: 0px;
    width: 100%;
    height: 3px;
    background-color: #2e81f4;
    
}

.header__right{
    display: flex;
    align-items: center;
}


.header__info{
    display: flex;
    align-items: center;
    cursor: pointer;
    padding: 4px 10px 4px 4px;
    margin-right: 4px;
}

.header__info:hover{
    background-color: #e4e6eb;
    border-radius: 25px;
}

.header__info > .MuiAvatar-root{
    height: 35px;
    width: auto;
}

.header__info > h4{
    margin-left: 10px;
}


.sidebar{
    padding: 25px 10px;
    width: 100%;
    flex: 0.33 1 123px;
    align-self: flex-start;
    position: -webkit-sticky;
    position: sticky;
    top: 64px;
    left: 0px;
}

.sidebar > .sidebar__top{
    /* height: 40vh; */
    width: 100%;
    border-bottom: 1px solid lightgray;
    margin-bottom: 10px;
    /* overflow: auto; */
}

.sidebar > .sidebar__bottom{
    /* height: 40vh; */
    width: 100%;
    /* overflow: auto; */
}



.sidebarRow{
    display: flex;
    align-items: center;
    padding: 10px 20px;
    cursor: pointer;
}

.sidebarRow:hover{
    background-color: lightgray;
    border-radius: 10px;
}

.sidebarRow > .sidebarRow__image{
    width: 36px;
    height: 36px;
    border-radius: 10px;
}

.sidebarRow > h4{
    color: #0a0a0a;
    margin-left: 20px;
}
.feed{
    flex: 1 1;
    padding: 30px 85px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.storyReel{
    display: flex;
    overflow: hidden;
    justify-content: space-between;
}
.story{
    position: relative;
    width: 120px;
    height: 200px;
    box-shadow: 0px 5px 17px -7px rgba(0, 0, 0, 0.75);
    border-radius: 10px;
    margin: 0px 5px;
    cursor: pointer;
    margin-top: 0;
    overflow: hidden;
}

.story__image{
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: -webkit-transform 200ms ease-in;
    transition: transform 200ms ease-in;
    transition: transform 200ms ease-in, -webkit-transform 200ms ease-in;
}

.story:hover img{
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
}

.story__avatar{
    margin: 10px;
    border: 4px solid #2e81f4;
}

.story > h4{
    position: absolute;
    bottom: 20px;
    left: 20px;
    color: white;
}
.messageSender{
    display: flex;
    flex-direction: column;
    margin-top: 30px;
    background-color: white;
    border-radius: 15px;
    box-shadow: 0px 5px 7px -7px rgba(0,0,0,0.75);
    width: 100%;
}

.messageSender__top{
    display: flex;
    border-bottom: 1px solid #eff2f5;
    padding: 15px;
    align-items: center;
}

.messageSender__top > form{
    flex: 1 1;
    display: flex;
    height: 40px;
}

.messageSender__top > form > input{
    flex: 1 1;
    outline: none;
    border: none;
    padding: 5px 20px;
    margin: 0px 10px;
    border-radius: 33px;
    background-color: #eff2f5;
    height: 100%;
    font-size: large;
    cursor: pointer;
}

.messageSender__top > form > input:hover{
    background-color: rgba(0,0,0,0.1);
}


.messageSender__bottom{
    display: flex;
    justify-content: space-evenly;
    padding: 8px;
}

.messageSender__options{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 8px;
    color: gray;
}

.messageSender__options > h3{
    font-size: medium;
    margin-left: 5px;
    cursor: pointer;
}

.messageSender__options:hover{
    background-color: #eff2f5;
    border-radius: 10px;
}


.createPost{
    z-index: 200;
}

.btn-modal {
    padding: 10px 20px;
    display: block;
    margin: 100px auto 0;
    font-size: 18px;
    
}

.createPost__wrapper, .overlay {
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
}

.overlay {
    background: rgba(243, 243, 244, 0.8);
}

.createPost__content {
    position: absolute;
    top: 45%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    line-height: 1.4;
    background: white;
    padding: 14px 0px;
    border-radius: 10px;
    max-width: 500px;
    width: 80%;
    z-index: 201;
}

.remove__Img-btn,
.createPost__close{
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 7px 7px;
    background-color: RGB(228, 230, 235);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.remove__Img-btn{
    padding: 2px 2px;
    border: 1px solid RGB(236, 237, 239);
    background: white;
}

.createPost__close:hover{
    background-color: rgba(0,0,0,0.1);
}

.createPost__content-top{
    display: flex;
    flex-direction: column;
}

.createPost__content-top > h3{
    flex: 1 1;
    text-align: center;
    padding: 0px 5px 20px;
    font-size: 22px;
    border-bottom: 1px solid rgba(0,0,0,0.1);
}

.contentPost__topInfo{
    display: flex;
    align-items: center;
    margin: 0px 16px;
    padding: 16px 0px;
}

.post__info{
    display: flex;
    flex-direction: column;
}

.post__status{
    padding: 0px 6px;
    border-radius: 6px;
    background-color: #e4e6eb;
    cursor: pointer;
}

.contentPost__middle{
    padding: 0px 16px 0px 16px;
    display: flex;
    flex-direction: column;
    max-height: 320px;
    overflow: auto;
}

.contentPost__middle::-webkit-scrollbar{
    width: 10px;
	background-color: #F5F5F5;
}

.contentPost__middle::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: gray;
}

.post__inputText >textarea{
    font-size: 1.5rem;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    outline: none;
    border: none;
    resize: none;
    width: 100%;
}



.post__inputText > textarea::-webkit-scrollbar{
    display: none;
}


.post__wrapper-image{
    padding: 6px;
    border-radius: 6px;
    border: 1px solid gray;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    background-color: RGB(247, 248, 250);
    background-clip:content-box;
    cursor: pointer;
}

.post__wrapper-image:hover{
    background-color: rgba(0,0,0,0.1);
}



.post__wrapper-image--addImg{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 150px;
}

.post__addBg-and-Emotion{

    display: flex;
    justify-content: space-between;
    align-items: center;
}

.post__addBg-and-Emotion > img{
    width: 38px;
}

label[for="selectImg"]{
    width: 30px;
    height: 30px;
    margin: 0px 5px;
}

.post__iconAdd{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.contentPost__bottom{
    padding: 16px 0px;
}

.post__addImg{
    margin: 0px 16px;
    border: 1px solid rgba(0,0,0,0.1);
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 8px;
}

.post__addImg > h4{
    color: #050505;
    padding: 0px 8px;
    font-size: 15px;
}

.post__btn-wrapper{
    padding: 16px 16px 0px;
}

.post__btn{
    border-radius: 6px;
    cursor: pointer;
    padding: 10px 10px;
    text-align: center;
    font-weight: 500;
    font-size: 17px;
}

.post{
    width: 100%;
    margin-top: 15px;
    background-color: white;
    border-radius: 15px;
    box-shadow: 0px 5px 7px -7px rgba(0, 0, 0, 0.75);
}

.post__image > img{
    width: 100%;
    display: block;
    max-height: 960px;
}

.post__top{
    display: flex;
    position: relative;
    align-items: center;
    padding: 15px;
    justify-content: space-between;
}

.post__top--wrapperInfo{
    display: flex;
}

.post__avatar{
    margin-right: 10px;

}

.post__topInfo > h3{
    font-size: medium;
}

.post__topInfo > p{
    font-size: small;
    color: gray;
}

.post__ShowRemoveAlert{
    display: flex;
    align-items: center;
    justify-self: center;
    border-radius: 50%;
    padding: 3px 3px;
    cursor: pointer;
    position: relative;
}

.post__ShowRemoveAlert:hover{
    background-color: rgba(0, 0, 0, 0.1);
}

.post__RemoveAlert{
    position: absolute;
    bottom: -40px;
    right: 10px;
    display: flex;
    align-items: center;
    border-radius: 5px;
    width: 309px;
    background-color: white;
    padding: 10px 5px;
    cursor: pointer;
}

.post__RemoveAlert:hover{
    background-color: rgba(0, 0, 0, 0.1);
}

.post__RemoveAlert h4{
    font-size: 15px;

}

.post__RemoveAlert p{
    font-size: 12px;
    color:#65676B;
}

.post__bottom{
    margin-top: 10px;
    margin-bottom: 0px;
    
}

.post__bottom > p{
    padding: 0px 15px 15px;
    word-break: break-all;
}

.post__count-like-and-comment{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 0px;
    margin: 0px 16px;
}

.post__count-like-and-comment > div:first-child{
    display: flex;
    align-items: center;
}

.post__count-like-and-comment > div:last-child:hover{
    cursor: pointer;
    text-decoration: underline;
}

.post__count-like-and-comment span{
    margin-left: 5px;
    transition: all 0.2s ease-in;
    cursor: pointer;
}

.post__count-like-and-comment span:hover{
    text-decoration: underline;
}

.post__options{
    display: flex;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
    justify-content: space-evenly;
    color: gray;
    padding: 4px;
}

.post__option{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px;
    flex: 1 1;
    cursor: pointer;
}

.post__option > p{
    margin-left: 10px;
}

.post__option:hover{
    background-color: #eff2f5;
    border-radius: 15px;
}


.post__comment-input{
    display: flex;
    padding: 4px 16px;
}

.comment__input{
    flex: 1 1;
    border-radius: 15px;
    background-color: RGB(240, 242, 245);
    display: flex;
    padding: 0 12px;
    align-items: center;
}

.comment__input-text{
    background-color: inherit;
    font-size: 16px;
    outline: none;
    border: none;
    flex: 1 1;
    padding: 10px 0px;
    resize: none;
    line-height: 1;
    overflow: auto;
    height: 37px;
    font-family: inherit;
}


/* commponent*/
.comment-item{
    display: flex;
    padding: 4px 0px 0px 16px;
}

.comment__avatar{
    margin-right: 5px;
}

.comment__content{
    border-radius: 15px;
    background-color: RGB(240, 242, 245);
    padding: 8px 12px;

}

.comment__content > h4{
    font-size: 13px;
    font-weight: 500;
}

.comment__content > p{
    font-size: 15px;
}

.comment__note{
    font-size: 12px;
    font-weight: bold;
    color: #65676B;
}


.widgets{
    padding: 25px 10px;
    flex: 0.33 1 123px;
    width: 100%;
    align-self: flex-start;
    position: -webkit-sticky;
    position: sticky;
    top: 64px;
    left: 0px;
    height: 90vh;
    overflow: auto;
    background-color: rgba(0,0,0,0);
    -webkit-background-clip: text; /*chỉ hieu ung cho scrollbar*/
    transition: background-color .5s;
}

/* css scrollbar */
.widgets::-webkit-scrollbar{ /*nền scroll*/
    width: 8px;
	background-color: #F5F5F5;
    
}

.widgets:hover{
    background-color: rgba(0,0,0,.164);
}

.widgets::-webkit-scrollbar-thumb{
    display: none;
}

.widgets:hover::-webkit-scrollbar-thumb
{
    display: block;
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px inherit;
	background-color: inherit;
}


/* end-css-scrollbar */
.widgets__navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: gray;
}

.widgets__icon{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
