.sidebarRow{
    display: flex;
    align-items: center;
    padding: 10px 20px;
    cursor: pointer;
}

.sidebarRow:hover{
    background-color: lightgray;
    border-radius: 10px;
}

.sidebarRow > .sidebarRow__image{
    width: 36px;
    height: 36px;
    border-radius: 10px;
}

.sidebarRow > h4{
    color: #0a0a0a;
    margin-left: 20px;
}