.post{
    width: 100%;
    margin-top: 15px;
    background-color: white;
    border-radius: 15px;
    box-shadow: 0px 5px 7px -7px rgba(0, 0, 0, 0.75);
}

.post__image > img{
    width: 100%;
    display: block;
    max-height: 960px;
}

.post__top{
    display: flex;
    position: relative;
    align-items: center;
    padding: 15px;
    justify-content: space-between;
}

.post__top--wrapperInfo{
    display: flex;
}

.post__avatar{
    margin-right: 10px;

}

.post__topInfo > h3{
    font-size: medium;
}

.post__topInfo > p{
    font-size: small;
    color: gray;
}

.post__ShowRemoveAlert{
    display: flex;
    align-items: center;
    justify-self: center;
    border-radius: 50%;
    padding: 3px 3px;
    cursor: pointer;
    position: relative;
}

.post__ShowRemoveAlert:hover{
    background-color: rgba(0, 0, 0, 0.1);
}

.post__RemoveAlert{
    position: absolute;
    bottom: -40px;
    right: 10px;
    display: flex;
    align-items: center;
    border-radius: 5px;
    width: 309px;
    background-color: white;
    padding: 10px 5px;
    cursor: pointer;
}

.post__RemoveAlert:hover{
    background-color: rgba(0, 0, 0, 0.1);
}

.post__RemoveAlert h4{
    font-size: 15px;

}

.post__RemoveAlert p{
    font-size: 12px;
    color:#65676B;
}

.post__bottom{
    margin-top: 10px;
    margin-bottom: 0px;
    
}

.post__bottom > p{
    padding: 0px 15px 15px;
    word-break: break-all;
}

.post__count-like-and-comment{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 0px;
    margin: 0px 16px;
}

.post__count-like-and-comment > div:first-child{
    display: flex;
    align-items: center;
}

.post__count-like-and-comment > div:last-child:hover{
    cursor: pointer;
    text-decoration: underline;
}

.post__count-like-and-comment span{
    margin-left: 5px;
    transition: all 0.2s ease-in;
    cursor: pointer;
}

.post__count-like-and-comment span:hover{
    text-decoration: underline;
}

.post__options{
    display: flex;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
    justify-content: space-evenly;
    color: gray;
    padding: 4px;
}

.post__option{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px;
    flex: 1;
    cursor: pointer;
}

.post__option > p{
    margin-left: 10px;
}

.post__option:hover{
    background-color: #eff2f5;
    border-radius: 15px;
}


.post__comment-input{
    display: flex;
    padding: 4px 16px;
}

.comment__input{
    flex: 1;
    border-radius: 15px;
    background-color: RGB(240, 242, 245);
    display: flex;
    padding: 0 12px;
    align-items: center;
}

.comment__input-text{
    background-color: inherit;
    font-size: 16px;
    outline: none;
    border: none;
    -ms-flex: 1 1;
    flex: 1 1;
    padding: 10px 0px;
    resize: none;
    line-height: 1;
    overflow: auto;
    height: 37px;
    font-family: inherit;
}


/* commponent*/
.comment-item{
    display: flex;
    padding: 4px 0px 0px 16px;
}

.comment__avatar{
    margin-right: 5px;
}

.comment__content{
    border-radius: 15px;
    background-color: RGB(240, 242, 245);
    padding: 8px 12px;

}

.comment__content > h4{
    font-size: 13px;
    font-weight: 500;
}

.comment__content > p{
    font-size: 15px;
}

.comment__note{
    font-size: 12px;
    font-weight: bold;
    color: #65676B;
}

